/* stylelint-disable no-duplicate-selectors */
/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors */
/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors */
.jc-progress {
  font-family: '\5FAE\8F6F\96C5\9ED1', '\5b8b\4f53', 'tahoma', 'arial';
  font-size: 12px;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.65);
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-block;
}
.jc-progress-line {
  width: 100%;
  font-size: 12px;
  position: relative;
}
.jc-progress-small.jc-progress-line,
.jc-progress-small.jc-progress-line .jc-progress-text .jcicon {
  font-size: 10px;
}
.jc-progress-outer {
  display: inline-block;
  width: 100%;
  margin-right: 0;
  padding-right: 0;
}
.jc-progress-show-info .jc-progress-outer {
  padding-right: calc(2em + 8px);
  margin-right: calc(-2em - 8px);
}
.jc-progress-inner {
  display: inline-block;
  width: 100%;
  background-color: #f5f5f5;
  border-radius: 100px;
  vertical-align: middle;
  position: relative;
}
.jc-progress-circle-trail {
  stroke: #f5f5f5;
}
.jc-progress-circle-path {
  stroke: #1890ff;
  animation: jc-progress-appear 0.3s;
}
.jc-progress-success-bg,
.jc-progress-bg {
  border-radius: 100px;
  background-color: #1890ff;
  transition: all 0.4s cubic-bezier(0.08, 0.82, 0.17, 1) 0s;
  position: relative;
}
.jc-progress-success-bg {
  background-color: #52c41a;
  position: absolute;
  top: 0;
  left: 0;
}
.jc-progress-text {
  word-break: normal;
  width: 2em;
  text-align: left;
  font-size: 1em;
  margin-left: 8px;
  vertical-align: middle;
  display: inline-block;
  color: rgba(0, 0, 0, 0.45);
  line-height: 1;
}
.jc-progress-text .jcicon {
  font-size: 12px;
}
.jc-progress-status-active .jc-progress-bg:before {
  content: "";
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  border-radius: 10px;
  animation: jc-progress-active 2.4s cubic-bezier(0.23, 1, 0.32, 1) infinite;
}
.jc-progress-status-exception .jc-progress-bg {
  background-color: #f5222d;
}
.jc-progress-status-exception .jc-progress-text {
  color: #f5222d;
}
.jc-progress-status-exception .jc-progress-circle-path {
  stroke: #f5222d;
}
.jc-progress-status-success .jc-progress-bg {
  background-color: #52c41a;
}
.jc-progress-status-success .jc-progress-text {
  color: #52c41a;
}
.jc-progress-status-success .jc-progress-circle-path {
  stroke: #52c41a;
}
.jc-progress-circle .jc-progress-inner {
  position: relative;
  line-height: 1;
  background-color: transparent;
}
.jc-progress-circle .jc-progress-text {
  display: block;
  position: absolute;
  width: 100%;
  text-align: center;
  line-height: 1;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  margin: 0;
  color: rgba(0, 0, 0, 0.65);
}
.jc-progress-circle .jc-progress-text .jcicon {
  font-size: 1.16666667em;
}
.jc-progress-circle.jc-progress-status-exception .jc-progress-text {
  color: #f5222d;
}
.jc-progress-circle.jc-progress-status-success .jc-progress-text {
  color: #52c41a;
}
@keyframes jc-progress-active {
  0% {
    opacity: 0.1;
    width: 0;
  }
  20% {
    opacity: 0.5;
    width: 0;
  }
  100% {
    opacity: 0;
    width: 100%;
  }
}
