.avatarwrapper {
  position: relative;
  width: 100px;
  height: 100px;
  border: 1px solid #e5e5e5;
  margin-bottom: 10px;
}
.avatarwrapperadd {
  position: relative;
  width: 100px;
  height: 100px;
  border: 1px dashed #e5e5e5;
  margin-bottom: 10px;
  background-color: #f7f7f7;
}
.avatarinput {
  width: 100%;
}
.avatarcontainer {
  width: 100px;
  height: 150px;
  position: relative;
  float: left;
  margin: 15px 23px 0 0;
}
.avatarfl {
  float: left;
}
.avatardelete {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 22px;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  text-align: center;
  line-height: 22px;
  opacity: 0;
  filter: alpha(opacity=0);
}
.avatardelete2 {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 22px;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  text-align: center;
  line-height: 22px;
}
.avatarwrapper:hover .avatardelete {
  opacity: 1;
  filter: alpha(opacity=100);
}
.lUpload {
  float: left;
}
.lUpload:hover .avatardelete {
  opacity: 1;
  filter: alpha(opacity=100);
}
